/* Infiniforge Stuff */

.canvas-container {
    width: 100%;
    height: 600px;
    position: relative;
}

canvas {
    width: 100%;
    height: 100%;
}

canvas {
    width: 100%;
}

div#game-container {
    width: 100%;
}

@media only screen and (max-width: 500px) {
    #gui {
        width: 100%;
    }

    .canvas-container {
        width: 100%;
        height: 65vh;
        position: relative;
    }

    #download-button {
        position: block;
        width: 100%;
    }
}

#gui {
    position: absolute;
    top: 0px;
    left: 0px;
}

#download-button-anchor {
    text-decoration: none;
}

#download-button {
    position: absolute;
    bottom: 10px;
    right: 10px;
    height: 60px;
    width: 175px;
}

#download-button button {
    background-color: grey;
    width: 100%;
    height: 100%;
    font-weight: bold;
    color: white;
    box-shadow: black;
}

#download-button button:hover {
    background-color: rgb(212, 212, 212);
}

#download-button button:active {
    background-color: rgb(202, 162, 162);
}
